.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* 增大左右内边距，避免显得拥挤 */
  padding: 1rem 3rem;
  background-color: #fff;
  border-bottom: 1px solid #ddd;
}

.header .logo {
  font-size: 1.8rem;
  font-weight: bold;
  color: #333;
}

/* 导航菜单 */
.header nav ul {
  list-style: none;
  display: flex;
  gap: 2rem; /* 增大导航间距 */
  margin: 0;
  padding: 0;
}

.header nav ul li a {
  font-size: 1.125rem;
  color: #333;
  position: relative;
  transition: color 0.3s ease;
}

.header nav ul li a:hover {
  color: #dca4a4;
}

/* 语言切换按钮 */
.language-switcher {
  border: 1px solid #dca4a4;
  background-color: #f8f4eb;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  color: #dca4a4;
  transition: all 0.3s ease;
}

.language-switcher:hover {
  background-color: #dca4a4;
  color: #fff;
}