.business {
  background-color: #fff; 
  text-align: center; /* 让大标题和内容居中 */
}

.business h2 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  color: #333;
  position: relative;
  display: inline-block;
}

.business h2::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #dca4a4;
  margin: 0.5rem auto;
}

/* 让列表整体居中，并去掉默认的列表样式 */
.business ul {
  list-style: none;        /* 去除默认子弹 */
  padding: 0;
  margin: 2rem auto;
  max-width: 800px;        /* 控制列表最大宽度 */
  text-align: center;      /* 让列表中的文字居中 */
}

/* 调整每一条业务的样式 */
.business ul li {
  font-size: 1.125rem;
  color: #555;
  margin-bottom: 1rem;
  line-height: 1.8;
}