.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 60vh;
  /* 你的 Hero 背景图（若没有可注释掉） */
  background: url('../assets/hero-bg.jpg') no-repeat center center/cover;
  text-align: center;
  color: #fff;
  padding: 2rem;
}

/* 半透明遮罩层，增强文字可读性 */
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 0;
}

/* 确保文本在遮罩层之上，并增加文字阴影 */
.hero h1,
.hero p {
  position: relative;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.5rem;
}