.contact {
  background-color: #fefefe;
  text-align: center;
}

.contact h2 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  color: #333;
  position: relative;
  display: inline-block;
}

.contact h2::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #dca4a4;
  margin: 0.5rem auto;
}

.contact p {
  font-size: 1.125rem;
  color: #555;
  max-width: 800px;
  margin: 1.5rem auto 0;
  line-height: 1.8;
}