.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  background-color: #f4f4f4;
  margin: 0;
  font-family: Arial, sans-serif;
}
/* 1. 全局样式：设置默认字体、背景色、页面间距等 */
html,
body {
  margin: 0;
  padding: 0;
  font-family: 'Noto Sans JP', 'Microsoft YaHei', sans-serif;
  background-color: #f8f4eb; /* 浅米色背景 */
  background-image: url('./assets/sakura-pattern.png');
  background-repeat: repeat;         /* 平铺 */
  background-position: top left;
}

/* 3. 全局的段落和标题基础设置 */
h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-family: 'Noto Serif JP', serif; /* 为标题使用更有韵味的衬线字体 */
}

p {
  margin: 0;
  padding: 0;
  line-height: 1.8;
}

/* 4. 为每个 section 统一留出上下间距 */
section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

/* 5. 链接的默认样式 */
a {
  text-decoration: none;
  color: inherit;
}

/* 如果想让鼠标悬停时有颜色变化，可以加上 */
a:hover {
  color: #dca4a4;
}