.about {
  background-color: #fefefe; /* 与浅米色有一点对比，也可直接用透明 */
  text-align: center;
  margin-bottom: 2rem; /* 可根据需要调整 */
}

.about h2 {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  color: #333;
  position: relative;
  display: inline-block;
}

/* 在标题下方加一条淡粉色线做装饰 */
.about h2::after {
  content: "";
  display: block;
  width: 60px;
  height: 3px;
  background-color: #dca4a4;
  margin: 0.5rem auto;
}

.about p {
  font-size: 1.125rem;
  color: #555;
  max-width: 800px;
  margin: 1.5rem auto 0;
  line-height: 1.8;
}